/*Task Create Btn*/

input{
  font-family: "Source Code Pro", Helvetica; 
  color: #FF6AC2;
  font-weight: bold;
  outline: 0;
}

::placeholder {
  color: #5c12d25a;
  opacity: 1; }



.AddTask{
  font-family: "Source Code Pro", Helvetica;
  box-shadow: -5px 2px 2px #FF6AC2;
  align-items: center;
  display: flex;
  border-radius: 10px;
  background: #f9f9f9;
  color: #5d12d2;
  height: 40px;
  margin: 20px;
  
}

.AddTask input {
 flex:1;
 border: none;
 background: transparent;
 margin-left: 20px;
 margin-right: 20px;
}

.AddTask img{
  margin-right: 5px;
 }

.AddTask button{
  font-family: "Source Code Pro", Helvetica;
  font-weight: 550;
  color: #f9f9f9;
  font-size: 18px;
  width: 13%;
  display: flex;
  align-items: center;
  border: none;
  outline: none;
  border-radius: 10px;
  background-color: #FF6AC2;
  cursor: pointer;
}

/*ul li*/
.taskList ul li{
  font-family: "Source Code Pro", Helvetica;
  font-weight: bold;
  list-style: none;
  font-size: 20px;
  padding: 25px 8px 10px 80px;
  user-select: none;
  cursor: pointer;
  position: relative;
  display: block;
  margin-right: 30px;
 
}


.taskList ul li::before{
  content: '';
  position: absolute;
  height:28px;
  width: 35px;
  border-radius: 50%;
  background-image: url(/dist/img/unchecked.png);
  background-size: cover;
  background-position: center;
  left: 40px;
  top: 20px;
}

.taskList ul li.checked{
  color:#5d12d2;
  text-decoration: line-through;

}
.taskList ul li.checked::before{
  background-image: url(/dist/img/checked.png);

}




.editing input {
  background-color: #f9f9f9;
  box-shadow: -1px 1px 3px #FF6AC2;
  border-radius: 5px;
  font-family: "Source Code Pro", Helvetica;
  font-weight: bold;
  color:#FF6AC2;
  border: 0;
  cursor: pointer;
  font-size: 15px;
  padding:10px;
  margin-bottom: 10px;
  outline: 0;
}